
.chart-gallery-grid img{
    vertical-align: middle;
    max-width: 100%;
    border: 1px solid rgba(192,200,201,1);
    
}


.charts-gallery{
    margin: 0px 10px;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    justify-content: space-between;
}
.charts-gallery > i {
  width: 190px;
  margin-right: 0px;
}
.chart-gallery-grid{
    margin: 10px 10px;
    width: 170px;
}
.chart-gallery-grid div{
  cursor: pointer;
  /* border: 1px solid rgba(192,200,201,1); */
  /* transition: all 0.4s; */
}
.chart-gallery-grid img:hover{
  cursor: pointer;
  border: 4px solid #65a1c6;
  /* transform: scale(1.05); */
}
.galary-description{
  width: 100%;
  max-width: 170px;
  display: inline-flex;
  justify-content: space-between;
  margin-top: 10px;
}

.galary-button{
  display: flex;
  justify-content: center;
  align-items: center;
}

.galary-button > button{
    background: #F6F8FA;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 12px;
    color: #000000;
    font-weight: 700;
    width: 60px;
    height: 23px;
}
.galary-button > button:hover{
  background-color: #CDD6DE;
  cursor: pointer;
}

.galary-word{
    font-family: Arial;
    color: #000000;
    text-align: justify;
    line-height: 24px;
    font-size: 12px;
    color: #000000;
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.gallery-Container{
    margin-top: 20px;
    margin-bottom: 50px;
    max-height: calc(80vh - 100px);
    overflow-y: auto;
    overflow-x: hidden;
}

@media screen and (max-width: 1088px) {
  .charts-gallery{
      justify-content:left !important;
  }
}

.chart-description-classify{
  font-family: "Helvetica";
  font-size: 16px;
  color: #000000;
  font-weight: 700;
  margin-left: 20px;
  list-style: disc;
}
