@import '~antd/dist/antd.css';
.header {
    padding-left: 20px;
    height       : 60px;
    border-bottom: solid 2px lightgray;
    display      : flex;
    align-items  : center;
    font-size    : 22px;
    font-weight  : bold;
    font-family: Arial-BoldMT;
    /* font-size: 24px; */
    color: #000000;
    font-weight: 700;   
}
.header a{
    margin-left: 10px;
    color: #000000;
}
.pannelWrapper {
    display       : flex;
    flex-direction: row;
    height        : calc(100% - 62px);
    /* border-bottom : solid 1px lightgray; */
    background-color: #F4F5F5;
}
.splitter-layout{
    height: calc(100% - 62px) !important;
}
.splitter-layout .layout-pane{
    background-color: #f4f5f5;
}

.editPannel {
    flex            : 1 1 auto;
    /* overflow        : scroll; */
    background-color: #FFFFFF;
    border    : solid 1px #CCCCCD;
    /* height: 730px;
    width: 737px; */
    height: 100%;
    /* margin-top: 16.5px;
    margin-left: 20px; */
}
/* 设置滚动条的样式 */
::-webkit-scrollbar {
    width:10px;
    height: 10px;
}
/* 滚动槽 */
::-webkit-scrollbar-track {
    border-radius:12px;
}
/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    border-radius:12px;
    background:rgba(0,0,0,0.1);
}
    
.chartPannel {
    flex: 0 0 auto;
}

pre {
    /* outline: 1px solid #ccc; */
    padding: 5px;
    margin : 5px;
    outline: none;
}

.string {
    color: green;
}

.number {
    color: darkorange;
}

.boolean {
    color: blue;
}

.null {
    color: magenta;
}

.key {
    color: red;
}

.topButton{
    border-radius: 2px;
    padding-left: 20px;
    padding-right: 20px;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-family: "ArialMT";
    font-size: 16px;
    color: #000000;
    font-weight: 400;
}

.topIconRun{
    background-size:cover;
    margin-right: 8px;
    width: 18px;
    height: 18px;
    background-image: url("../icon/run.png");
}
.topIconExport{
    background-size:cover;
    margin-right: 8px;
    width: 24px;
    height: 24px;
    background-image: url("../icon/export.png");
}
.topIconShare{
    background-size:cover;
    margin-right: 8px;
    width: 23px;
    height: 23px;
    background-image: url("../icon/share.png");
}
.topIconExamples{
    background-size:cover;
    margin-right: 8px;
    width: 20px;
    height: 20px;
    background-image: url("../icon/examples.png");
}


.topButton:hover{
    background-color: #F4F5F5;
}

.topButton > img{
    margin-right: 10px;
}

/* ---------------------------------------------------- */
.ant-modal-close{
    color: #000000;
}
.ant-modal-header {
    padding: 20px 0px 5px 20px;
    color: rgba(0, 0, 0, 0.85);
    background: #fff;
    border: none;
    /* border-bottom: 1px solid #f0f0f0; */
    /* border-radius: 2px 2px 0 0; */
}
.modalDivider{
   width: calc(100% - 40px);
   background-color: #979797 ;
   height: 1px;
   border: none;
}
.ant-modal-title {
    margin: 0;
    margin-top: 5px;
    line-height: 30px;
    word-wrap: break-word;
    font-family: Arial-BoldMT;
    font-size: 26px;
    color: #000000;
    font-weight: 700;
}

.ant-modal-body {
    padding: 0px;
    font-size: 14px;
    line-height: 1.5715;
    word-wrap: break-word;
}
.shareContent{
    padding: 25px 60px;
}
.shareButtonMix{
    display: flex;
    margin-bottom: 15px;
}
.shareLink{
    /* width: calc(100% - 40px); */
    width: calc(100% - 220px);
    padding: 9px 20px;
    background: #FFFFFF;
    border: 1px solid rgba(192,200,201,1);
    font-family: ArialMT;
    font-size: 16px;
    color: #000000;
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.shareURLTitle{
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}
.shareButton{
    margin-left: 20px;
    max-width: 110px;
    height: 44px;
    background: #F6F8FA;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}
.shareButton:hover{
    background: #CDD6DE;
    max-width: 110px;
    height: 44px;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}
.shareButton:focus{
   box-shadow: none;
   color: #000000;
   background: #CDD6DE;
   border: 1px solid rgba(192,200,201,1);
}
@media screen and (max-width: 1088px) {
    .ant-modal-centered .ant-modal{
        width: calc(100% - 10px) !important;
    }
}

.exportContent{
    padding: 25px 0px;
}

.exportImage{
    display: flex;
    align-content: flex-start;
    height: 52px;
    margin-right: 10px;
}

.exportPngImage{
    background-size:cover;
    width: 38px;
    height: 30px;
    background-image: url('../icon/png.png');
}
.exportJsonImage{
    background-size:cover;
    width: 35px;
    height: 25px;
    background-image: url('../icon/json.png');
}
.exportSvgImage{
    background-size:cover;
    width: 35px;
    height: 30px;
    background-image: url('../icon/svg.png');
}
.exportPdfImage{
    background-size:cover;
    width: 32px;
    height: 30px;
    background-image: url('../icon/pdf.png');
}
.exportHtmlImage{
    background-size:cover;
    width: 35px;
    height: 28px;
    background-image: url('../icon/html.png');
}

.exportRow{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 20px 50px;
    justify-content: space-between;
}

.exportButton{
    width: 130px;
    height: 44px;
    background: #F6F8FA;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}

.exportButton:hover{
    background: #CDD6DE;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}

.exportButton:focus{
    background: #CDD6DE;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 16px;
    color: #000000;
    font-weight: 700;
}


.exportDescription{
    width: 850px;
    margin-right: 10px;
}

.exportDescription > p{
    margin-bottom: 6px;
}

.exportContentDivider{
    margin-top: -10px;
    width: calc(100% - 100px);
    background-color: #DFDFDF;
    height: 1px;
    border: none;
}
.examples_chart .ant-modal-content{
    height: 80vh;
    overflow: hidden;
}

@media (max-width:757px){
    .topButtonText{
        display: none;
    }
    .header > a{
        font-size: 22px;
        white-space: nowrap;
    }
    .topButton{
        width: 100px;
        padding-left: 0px;
        padding-right: 0px;
    }
    .header{
        justify-content: space-between;
        padding-right: 10px;
        padding-left: 10px;
    }
    .topIconExport{
        margin-right:0px;
    }
    .pannelWrapper{
        display: flow-root;
        background-color: white;
    }
    /* .chartPannel{
        zoom: 0.8
    } */

}
@media (max-width:395px){
    .chartPannel{
        zoom: 0.7
    }
}

@media (max-width:562px){
    .topButton{
        width: 73px;
        padding-left: 0px;
        padding-right: 0px;
    }
}

@media (max-width:450px){
    .topButton{
        width: 40px;
        padding-left: 0px;
        padding-right: 0px;
    }
}

@media (max-width:500px){
    .exportDescription > p:nth-child(2){
        display: none;
    }
    .exportDescription > p{
        margin-bottom: 0px;
    }
    .exportImage{
        align-items: center
    }
}

@media(max-width: 450px){
    .shareButtonMix {
        display: flex;
        margin-bottom: 15px;
        flex-direction: column;
    }
    .shareLink {
        width: 100%;
        margin-bottom: 0px;
    }
    .shareButton, .shareButton:hover{
        margin-left: 0px;
        max-width: 100%;
        margin-top: 10px;
    }
}

@media(max-width: 340px){
    .exportImage{
        display: none;
    }
}